import EmployeeComponent from './employee/Emp.vue'
import UpdateComponent from './employee/update.vue'
import EmpTotal from "./employee/EmpTotal.vue"
export const employee=
{
  path: '/employee',
  components:{
    default:EmpTotal
  },
  children: [
    {
        path: 'list',
        component: EmployeeComponent,
        name: 'listEmloyee',  
    },
  	{
  		  path: 'update/:id',
        component: UpdateComponent,
        name: 'updateEmloyee',	
  	}
    
  ]
}


