import Vue from 'vue'
import store from './stores'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueBus from 'vue-bus';


Vue.use(ElementUI);
Vue.use(VueBus);
import {CONFIG} from './assets/js/config/index.js'
Vue.use(CONFIG);

Vue.use(MintUI)
import indexName from './page/index.vue'
import hello from './components/Hello.vue'
import vuev from './components/src/swipe.vue'
import Creat from './page/Creat.vue'
import VueResource from 'vue-resource'
import Vuex from 'vuex'
Vue.use(Vuex)
Vue.use(VueResource)
import {ceshi} from './ceshi.js'
import {es} from './es6.js'
import {kugous} from './kugou.js'
import main from './page/main.vue'
import {buke} from './buke.js'
import {vvs} from './vv.js'
import {dd} from './child.js'
import {hbox} from './hh.js'
import Counter from './page/Counter.vue'
import Githup from './Githup.vue'
import {onset} from './dongtai.js'
import shareName from './share/share.vue'
import {employee} from './employee.js'
import {el} from './ele.js'


import zjxAkert from './alertName/index.js'

// Vue.component('zjxAkert',zjxAkert)
Vue.use(zjxAkert)






const router = new VueRouter({
    //mode: 'history',//是否添加*
    //base: '/TestDemo/',//获取当前文件所在目录的完整目录名
    base:__dirname,
    routes: [
      {path: '/', components:{default:indexName} ,name: 'indexName',},
      {path: '/index/:name', component: indexName},
      {path: '/hello', component: hello},
      {path: '/vue', component: vuev},
      {path: '/main', component: main},
      {path: '/counter', component: Counter},
      {path: '/githup', component: Githup},
      {path: '/share', component: shareName},
      ceshi,
      buke,
      vvs,
      dd,
      hbox,
      es,
      kugous,
      onset,
      employee,
      el,
    ]
  },

)
/* eslint-disable no-new */
var vm = new Vue({
  router,
  store,
  template: `
    <div>
      <transition name="fade" mode="out-in">
      <keep-alive>
       <router-view></router-view>
      </keep-alive>


      </transition>

    </div>
  `
}).$mount("#app");



