<template lang="html">
	<div>
			<h3>修改员工信息</h3>
			<form @submit.prevent="submit">
				<table border="1">
					<tr>
						<input type="hidden" name="id" v-model="paramd.id"/>
						<th>姓名</th>
						<td><input type="text" name="name" v-model="paramd.name" /></td>
					</tr>
					<tr>
						<th>性别</th>
						<td>					
						<input type="radio" name="sex" value="男" v-model="paramd.picked"/>男
						<input type="radio" name="sex" value="女" v-model="paramd.picked"/>女		
						</td>
					</tr>
					<tr>
						<th>年龄</th>
						<td><input type="text" name="age" v-model="paramd.age" /></td>
					</tr>
					<tr>
						<th>手机</th>
						<td><input type="text" name="phone" v-model="paramd.phone" /></td>
					</tr>
					<tr>
						<td colspan="2" align="center">
							<input type="submit" value="修改" />&nbsp;
							<input type="reset" value="重置" /></td>
					</tr>
				</table>
			</form>
		</div>

</template>

<script>
export default {
  name: 'updateEmloyee',
  data(){
    return{
    	picked:"",
      	paramd:{},
    }
  },
  mounted(){
   	this.$http.post(
        '/employeesys_ssh/findById/'+this.$route.params.id,
        ).then((res)=>{
    //       this.paramd.id = res.body.msg.id;
    //       this.paramd.name = res.body.msg.name;
    //       this.paramd.picked = res.body.msg.sex;
    //       this.paramd.age = res.body.msg.age;
		  // this.$set(this.paramd,"phone",res.body.msg.phone)
		this.paramd = Object.assign({},this.paramd,{
			id:res.body.msg.id,
			name:res.body.msg.name,
			picked:res.body.msg.sex,
			age:res.body.msg.age,
			phone:res.body.msg.phone
		})
      })
  },
  methods:{
  	 submit: function() {
		console.log(this.paramd);
	}
	}
}
</script>
<style type="text/css">
			table {
				width: 300px;
				border-collapse: collapse;
			}
			
			h3 {
				text-align: center;
			}
			
			
			.error{
				color:red;
				font-size:10px;
			}
		</style>


