<template lang="html">
   <div>
      <h3>所有员工信息</h3>
      <a href="add.jsp">添加员工信息</a><br/>
      <table border="1">
      <tr><th>序号</th><th>姓名</th><th>性别</th><th>年龄</th><th>手机</th><th>操作</th></tr>
         <tr v-for="item in list">
            <td>{{item.id}}</td><td>{{item.name}}</td>
              <td>{{item.sex}}</td><td>{{item.age}}</td><td>{{item.phone}}</td>
              <td>
              <router-link
                :to="{name:'updateEmloyee',params:{id:item.id}}">修改</router-link>
              <a style="cursor: pointer;" onclick="deleteL(1)">删除
              </a>
            </td>
         </tr>
      </table>
    </div>

</template>

<script>
export default {
  name: 'listEmloyee',
  data(){
    return{
      list:[]
    }
  },
  created(){
    this.$http.get(
        '/employeesys_ssh/list',
        ).then((res)=>{
          this.list = res.body.msg;
        console.log(res)
      })
  },
  mounted:function(){
    
  }
}
</script>

<style lang="css">
  table td {
        text-align: center;
      }
      /*合并表格的边框*/
      
      
      
      h3 {
        text-align: center;
      }
      
      
</style>



